import React, { useState } from 'react';
import API from './API';
import './lesson_3';
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import SearchIcon from '@mui/icons-material/Search';
import DirectionsIcon from '@mui/icons-material/Directions';
import { Button } from "@mui/material";


const Lesson3 = () => {
    const [searchName, setSearchName] = useState<string>('');
    const [searchResult, setSearchResult] = useState<ResultItemType[] | string>([]);
    const [searchNameByType, setSearchNameByType] = useState<string>('');
    const [searchResultByType, setSearchResultByType] = useState<ResultItemType[] | string>([]);

    type ResultItemType = {
        Poster: string
        Title: string
        Type: string
        Year: string
        imdbID: string
    }

    // const searchFilm = () => {
    //     API.searchFilmsByTitle(searchName)
    //         .then( ({ data} ) => {
    //             const { Response, Search, Error } = data;
    //             if (Response === 'True') {
    //                 setSerachResult(JSON.stringify(Search));
    //             } else {
    //                 setSerachResult(Error);
    //             }
    //         })
    //         .catch(err => {
    //             console.log('err ', err);
    //         })
    // };

    const searchFilm = async () => {
        try {
            const {data} = await API.searchFilmsByTitle(searchName);
            const {Response, Search, Error} = data;
            Response === 'True' ? setSearchResult(Search) : setSearchResult(Error);
        } catch (err) {
            console.log('err ', err);
        } finally {
            setSearchName('');
        }
    };

    const searchByType = async (e: React.MouseEvent<HTMLButtonElement>) => {
        try {
            const type: string = e.currentTarget.dataset.t ? e.currentTarget.dataset.t : '';
            const {data} = await API.searchFilmsByType(searchNameByType, type);
            console.log(data)
            const {Response, Search, Error} = data;


            Response === 'True'
                ? setSearchResultByType(Search)
                : setSearchResultByType(Error);
        } catch (err) {
            console.log('err ', err);
        } finally {
            setSearchNameByType('');
        }
    };

    return (
        <>
            <h1>Films search</h1>
            <div>
                <div className={'search-block'}>
                    <Paper
                        sx={{p: '2px 4px', display: 'flex', alignItems: 'center', maxWidth: '100%'}}
                    >
                        <InputBase
                            sx={{ml: 1, flex: 1}}
                            placeholder="Search by name"
                            inputProps={{'aria-label': 'Search by name'}}
                            value={searchName}
                            onChange={(e) => setSearchName(e.currentTarget.value)}
                        />
                        <SearchIcon style={{opacity: 0.3}} />
                        <Divider sx={{height: 28, m: 0.5}} orientation="vertical" />
                        <IconButton color="primary" sx={{p: '10px'}} aria-label="directions" onClick={searchFilm}>
                            <DirectionsIcon />
                        </IconButton>
                    </Paper>

                </div>
                <ul className={'wrapper'}>
                    {
                        typeof searchResult !== 'string' ? searchResult.map((el: ResultItemType, i: number) => {
                            return (
                                <li className={'item'} key={i}>
                                    <img
                                        src={el.Poster === 'N/A' ? 'data:image/png;base64,' : el.Poster}
                                        alt="poster" />
                                    <div className={'content'}>
                                        <p><b>Title</b>: {el.Title}</p>
                                        <p><b>Type</b>: {el.Type}</p>
                                        <p><b>Year</b>: {el.Year}</p>
                                    </div>
                                </li>
                            )
                        }) : searchResult
                    }
                </ul>


                <div className={'search-block'}>
                    <Paper
                        sx={{p: '2px 4px', display: 'flex', alignItems: 'center', maxWidth: '100%'}}
                    >
                        <InputBase
                            sx={{ml: 1, flex: 1}}
                            placeholder="Search by type"
                            inputProps={{'aria-label': 'Search by name'}}
                            value={searchNameByType}
                            onChange={(e) => setSearchNameByType(e.currentTarget.value)}
                        />
                        <SearchIcon style={{opacity: 0.3}} />
                        <Divider sx={{height: 28, m: 0.5}} orientation="vertical" />
                        <Button onClick={searchByType} data-t='movie'>Movie</Button>
                        <Divider sx={{height: 28, m: 0.5}} orientation="vertical" />
                        <Button onClick={searchByType} data-t='series'>Series</Button>
                    </Paper>
                </div>

                <ul className={'wrapper'}>
                    {
                        typeof searchResultByType !== 'string' ? searchResultByType.map((el: ResultItemType, i: number) => {
                            return (
                                <li className={'item'} key={i}>
                                    <img
                                        src={el.Poster === 'N/A' ? 'data:image/png;base64,' : el.Poster}
                                        alt="poster" />
                                    <div className={'content'}>
                                        <p><b>Title</b>: {el.Title}</p>
                                        <p><b>Type</b>: {el.Type}</p>
                                        <p><b>Year</b>: {el.Year}</p>
                                    </div>

                                </li>
                            )
                        }) : searchResultByType
                    }
                </ul>

            </div>

        </>
    );
}
export default Lesson3;